.header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Archivo";
  font-weight: 900;
}
.summary {
  /* width: 100%; */
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
}
.flex {
  display: flex;
}
.status {
  display: flex;
  flex-direction: column;
  width: 50vw;
  height: 100vh;
}
/* / */

@media only screen and (max-width: 1260px) {
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Archivo";
    font-weight: 900;
  }
  .status {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  .summary {
    width: 100%;
    display: flex;
    justify-content: space-around;
    /* align-items: flex-start; */
  }
  .flex {
    display: block;
  }
}
