.state-flex {
  display: flex;
  /* justify-content: space-evenly; */
  width: 100%;
  height: 100%;
}
.flex-temp {
  display: flex;
  justify-content: space-around;
  font-size: 1.3em;
  margin-top: 12px;
}
.flex-start {
  width: 20%;
  justify-content: flex-start;
  align-items: center;
}
.flex-end {
  justify-content: flex-end;
  align-items: center;
}
.ul {
  width: 100%;
  padding: 0;
  margin-left: 50px;
}
.flex-temp-p {
  display: flex;
  justify-content: space-evenly;
  margin-left: 2%;
  font-size: 1.2em;
}
.flex-start-p {
  width: 20%;
  justify-content: flex-start;
  align-items: center;
  background-color: rgb(241, 241, 241);
  border-radius: 12px;
  padding: 7px;
  font-weight: 900;
}
.flex-end-p {
  justify-content: flex-end;
  align-items: center;
  background-color: rgb(241, 241, 241);
  border-radius: 12px;
  padding: 6px;
  font-weight: 900;
}
@media only screen and (max-width: 600px) {
  .state-flex {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    margin-left: 0px;
  }
  .ul {
    width: 100%;
    padding: 0;
    margin-left: 0px;
  }
  .flex-temp-p {
    display: flex;
    justify-content: space-evenly;
    margin-left: 3%;
    font-size: 1.2em;
    border-radius: 12px;
  }
  .flex-start-p {
    width: 20%;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(241, 241, 241);
    border-radius: 12px;
    padding: 5px;
    font-weight: 900;
  }
  .flex-end-p {
    justify-content: flex-end;
    align-items: center;
    background-color: rgb(241, 241, 241);
    border-radius: 12px;
    padding: 5px;
    font-weight: 900;
  }
}
@media only screen and (max-width: 1260px) {
  .ul {
    width: 100%;
    padding: 0;
    margin-left: 5px;
  }
}
